<template>
  <div class="login">
    <div class="title">简易聊天室</div>
    <input class="name-input" type="text" v-model="userName" placeholder="请输入你的名字" />
    <div v-if="errTips">{{errTips}}</div>
    <button class="login-btn" @click="handleLogin">登录</button>
  </div>
</template>

<script>
import { randomWord } from "../util/index";
export default {
  name: "Login",
  data() {
    return {
      userName: "",
      errTips: ""
    };
  },
  sockets: {
    //不能改,j建立连接自动调用connect
    connect: function() {
      //与socket.io连接后回调
      console.log("socket connected");
    },
    //服务端向客户端发送login事件
    login: function(value) {
      //监听login(后端向前端emit  login的回调)
      console.log(value);
    }
  },
  methods: {
    async handleLogin() {
      this.$socket.emit("compile");
      this.errTips = "";
      if (!this.userName) {
        this.errTips = "请输出你的名字^_^";
        return;
      }

      let {
        data: { code, data }
      } = await this.$https.post("/api/login", {
        name: this.userName,
        id: randomWord(10)
      });

      if (code !== 0) {
        this.errTips = "登录失败";
        return;
      }

      this.$router.push(`/chat?id=${data.id}`);
    }
  }
};
</script>
